// Напиши функцію, яка приймає 2 числа і повертає :
// -1, якщо перше число менше, ніж друге;
// 1 - якщо перше число більше, ніж друге;
// 0 - якщо числа рівні.

function numReturn(x, y) {
    if (x < y) {
        console.log('-1');
    } else if (x > y) {
        console.log('1');
    } else {
        console.log('0');
    }
}

numReturn(100, 100);

